/*Logo*/
.App-logo {
  height: 5vmin;
  pointer-events: none;
  display: block;
  margin: auto;
}
/* Navbar container */
.navbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  overflow: hidden;
  background-color: #8f6963;
  font-family: Arial;
  padding: 0px;
}

/* Links inside the navbar */
.navbar a {
  float: none;
  display: flex;
  font-size: 16px;
  color: white;
  text-align: center;
  padding: 10px 10px;
  margin: 0;
  overflow: hidden;
  text-decoration: none;
}

/* The dropdown container */
.dropdown {
  float: left;
  overflow: hidden;
}

/* Dropdown button */
.dropdown .dropbtn {
  font-size: 16px;
  border: none;
  outline: none;
  color: white;
  padding: 14px 16px;
  background-color: inherit;
  font-family: inherit; /* Important for vertical align on mobile phones */
  margin: 0; /* Important for vertical align on mobile phones */
}

/* Add a red background color to navbar links on hover */
.navbar a:hover, .dropdown:hover .dropbtn {
  background-color: #d98e97;
}

/* Dropdown content (hidden by default) */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

/* Links inside the dropdown */
.dropdown-content a {
  float: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

/* Add a grey background color to dropdown links on hover */
.dropdown-content a:hover {
  background-color: #ddd;
}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
  display: block;
}